import React, { useState } from 'react';
import NavTabs from './NavTabs';
import Home from './pages/Home';
import About from './pages/About';
import Blog from './pages/Blog';
import Contact from './pages/Contact';

export default function PortfolioContainer() { // container
  const [currentPage, setCurrentPage] = useState('Home');

  // if we look at currentPage, and it is assigned HOME, we show that certain page related to Home
  const renderPage = () => { // must return some JSX component
    if (currentPage === 'Home') {
      return <Home />;
    }
    if (currentPage === 'About') {
      return <About />;
    }
    if (currentPage === 'Blog') {
      return <Blog />;
    }
    return <Contact />;
  };

  const handlePageChange = (page) => setCurrentPage(page);

  return (
    <div>
      {/* passing to Navtabs props.currentPage props.handlePageChange */}
      <NavTabs currentPage={currentPage} handlePageChange={handlePageChange} />
      {/* We run the renderPage function which was defined on line 12, so we can reevaluate the if statements and return the appropriate page */}
      {renderPage()}
    </div>
  );
}
